export const some = (object, fn) => {
    for (const key in object) {
        if (fn(object[key]))
            return true;
    }
    return false;
};
export const filterToArray = (object, fn) => {
    const filtered = [];
    for (const key in object) {
        if (fn(object[key]))
            filtered.push(object[key]);
    }
    return filtered;
};
export const filter = (object, fn) => {
    const filtered = {};
    for (const key in object) {
        if (fn(object[key]))
            filtered[key] = object[key];
    }
    return filtered;
};
export const size = (object) => {
    return Object.keys(object).length;
};
export const objectToArray = (object) => {
    return Object.values(object);
};
